import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Api } from 'core/services/api.service';

export class WFHPolicy {

  maxAllowed: number;
  allowedPeriod: number;
  approval: {
    required: boolean;
    users?: string[];
  }
  startDate: Date;
}

@Injectable()
export class WFHPolicyService extends Api {

  private _base_url: string = '/wfhPolicies';

  constructor(http: HttpClient) {

    super(http);
  }

  getWfhPolicy(): Promise<any> {

    return this.get(this._base_url);
  }

  putWfhPolicy(policy: any): Promise<any> {

    return this.get(this._base_url);
  }
}
