import { Component, OnInit, ViewEncapsulation, ViewChild } from '@angular/core';
import { SuccessModalComponent } from 'shared/components/success-modal/success-modal.component';
import { ErrorModalComponent } from 'shared/components/error-modal/error-modal.component';

import { ALLOCATION } from 'config/constants/wfh';
import { EmployeeService } from 'modules/employee/employee.service';
import { WFHPolicy, WFHPolicyService } from 'modules/work-from-home/policy/wfh-policy.service';

export class Approver {
  _id: string;
  itemName: string;
};

@Component({
  selector: 'app-wfh-policy',
  templateUrl: './manage-wfh-policy.component.html',
  styleUrls: ['./manage-wfh-policy.component.scss'],
  providers: [ WFHPolicyService, EmployeeService ],
  encapsulation: ViewEncapsulation.None
})
export class ManageWfhPolicyComponent implements OnInit {

  wfhPolicy: WFHPolicy;
  error: string;
  from_supervisor: boolean;

  // Properties for Additional Approvers Dropdown
  additionalApproversDropdownSettings = { };
  additionalApprovers: Approver [] = [];
  additionalApproversDropdownList: Approver[] = [];

  __wfhAllocationPeriodType = ALLOCATION;

  @ViewChild('app-error-modal') errorModal: ErrorModalComponent;
  @ViewChild('app-success-modal') successModal: SuccessModalComponent;

  constructor(private wfhPolicyService: WFHPolicyService, private employeeService: EmployeeService) {
    this.wfhPolicy = new WFHPolicy ();
    this.from_supervisor = false;
  }

  ngOnInit() {
    this.wfhPolicyService.getWfhPolicy ()
      .then(wfhPolicy => this.wfhPolicy = wfhPolicy)
      .catch(err => {
        if (err.status === 404) {
          // not a problem if the current policy is not found.. may be this is the first time.
          this.wfhPolicy.maxAllowed = 0;
          this.wfhPolicy.allowedPeriod = ALLOCATION.WEEKLY;
          this.wfhPolicy.approval.required = true;
          this.wfhPolicy.startDate = new Date();
          // end year not needed.

          return;
        }

        this.error = err;
        this.errorModal.show();
      });

    this.employeeService.getEmployees(null, 1, 25)
      .then(response => {
        console.log(response.users[1]);
        response.users.forEach (user => this.additionalApproversDropdownList.push ({
          _id: user._id,
          itemName: user.name.fullName()
        }));
      })
      .catch(err => {
        if (err.status === 404) {
          return;
        }

        this.error = err;
        this.errorModal.show();
      });

    this.additionalApproversDropdownSettings = {
          text: 'Select Users',
          selectAllText: 'Select All',
          enableSearchFilter: true
    };
  }

  onSubmit() {
    this.additionalApprovers.forEach (x => this.wfhPolicy.approval.users.push(x._id));

    this.wfhPolicyService.putWfhPolicy(this.wfhPolicy)
      .then(x => {
        this.error = '';
        this.successModal.show();
      })
      .catch (err => {
        this.error = err;
        this.errorModal.show();
      });
  }

}
